@use 'base/variables' as v;
@use 'base/mixins' as m;

.login{
    margin-top: 5rem;
    .uptask{
        @include m.textoDegradado(v.$cyan, v.$morado);
    }

    input[type="submit"]{
        @include m.boton(v.$azulM, v.$blanco);
    }
    .acciones{
        a{
            color: v.$azulM;
            font-weight: 700;
        }
    }
}
